.addMediaSection{
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    width: 90%;
    margin: auto;
    padding-bottom: 110px;
    border: 4px dashed #ccc;
    min-height: 500px;
    height: auto;
}
.filePrivew{
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;
    margin-bottom: 30px;
}
.boxPrivew{
    box-shadow: 0 3px 3px #ccc;
    width: 22.5%;
    margin-left: 10px;
    position: relative;
    padding: 10px;
    margin: 10px 10px;
}
.remove{
    position: absolute;
    top: -23px;
    left: -1px;
}
.remove:hover{
    color: red;
    cursor: pointer;
}
.boxPrivew img{
    width: 100%;
    height: 170px;
}
.addFile{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}
.inputFile{
    display: flex;
    flex-flow: column;
}
input[type="file"]{
    display: none;
}
.label{
    background-color: rgb(47, 178, 187);
    padding:5px 10px;
    border-radius: 10px;
    color: white;
    font-size: 15px;
}
.inputFile{
    padding: 50px;
    border: 4px dashed #ddd;
    margin-top: 10px;
    font-size: 20px;
}